/* Css for all media queries are here  */

body {
	background: radial-gradient(#e5e5e5, #ffff, #e5e5e5);
	width: 100%;
	height: 100%;
}
.user-profile {
	font-size: medium;
	color: azure;
	font-weight: bold;
	font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
		sans-serif;
}

#root {
	width: -webkit-fit-content !important;
	width: -moz-fit-content !important;
	width: fit-content !important;
	display: flex !important;
}

/* FOOTER CONTENTS */

footer {
	position: relative;
	padding: 50px;
	background: #111;
	margin-top: 0em;
	width: 100%;
}
.aboutus {
	display: flex;
}
footer h2 {
	position: relative;
	color: rgba(255, 250, 250, 0.979);
	margin-bottom: 15px;
	width: 10em;
	margin-left: 7px;
}
/* Handles the red line in the footer */
footer .container h2:before {
	content: "";
	position: absolute;
	bottom: -5px;
	width: 50px;
	height: 2px;
	background: #f00;
}

/* Handles the about us text */
footer p {
	color: #999;
	font-family: sans-serif;
}

/* Handles the footer icons */
.sci {
	margin-top: 20px;
	display: flex;
}
/* Removes the list bullets */
.sci li {
	list-style: none;
}
/* Handles the icons styling */
.sci a {
	width: 40px;
	height: 40px;
	background: #222;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-right: 10px;
	text-decoration: none;
	border-radius: 4px;
	color: #fff;
}
.sci li a:hover {
	background: #f00;
}
/* Handles icon size at footer*/
.sci li a .fa {
	color: #fff;
	font-size: 10px;
}
/* Handles the quicklinks */
.quickLinks ul li a {
	color: #999;
	text-decoration: none;
	margin-bottom: 10px;
	display: inline-block;
	font-size: 1em;
	font-family: sans-serif;
}
.quickLinks ul li a:hover {
	color: #fff;
}
/* Controls the margin from About Us to Quicklinks */
.quickLinks {
	margin-left: 7em;
}
.contact .info li {
	margin-bottom: 10px;
	list-style: none;
	font-family: sans-serif;
}
/* Handles icons on Contact Us list */
.contact .info li span:nth-child(1) {
	color: #fff;
	margin-right: 10px;
}
.contact .info li span {
	color: #999;
}
.contact .info li a {
	color: #999;
	text-decoration: none;
}
.contact .info li a:hover {
	color: #fff;
}
.copyrightText {
	background: #181818;
	padding: 8px 100px;
	text-align: center;
	color: #999;
	width: 100%;
}




/* MEDIA QUERIES */

@media (min-width: 300px) and (max-width:399.99px) {
	.container {
		margin-left: 1px;
		max-width: 25em;
	}

	/* Navbar elements to... */
	.navbar {
		width: 508px;
	}
	.NavbarItems {
		position: relative;
	}
	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 90vh;
		position: absolute;
		top: 80px;
		left: -100%;
		opacity: 1;
		transition: all 0.5s ease;
	}
	.nav-menu.active {
		background: #242222;
		left: 0;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
		padding-top: 2em;
		height: 20em;
		padding-left: 0em;

	}
	.nav-links {
		text-align: center;
		padding: 2rem;
		width: 100%;
		display: table;
	}
	.nav-links:hover {
		background-color: #1888ff;
		border-radius: 0;
	}
	.navbar-logo {
		position: absolute;
		top: -10px;
		left: 70px;
		-webkit-transform: translate(25%, 50%);
		        transform: translate(25%, 50%);
	}
	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
